import React from 'react';
import './FaqSection.scss'
import Faq from './Faq'

function FaqSection() {
 
  return (
    <div className='faq-section'>
       
        <div className='faq-section-inner '>
            <div className='tc-gold fs-l ffam-regular faq-title'>FAQ</div>
            <Faq
                question='How much will it cost to mint?'
                answer='Minting one NFT from the Moggie Rescue Club collection will cost 0.08 eth + gas fees. The Moggie Rescue Club Collection was created using the ERC721A standard, an improved implementation of the ERC721 standard that supports minting multiple tokens for close to the cost of one regarding gas fees.'
            />
            <Faq
                question='For how long will it be possible to mint?'
                answer='Mint will be available for a countdown of 10 days, when we’ll open the gates of the Moggie Rescue Club. After this period, we’ll be closing the gates and the mint will officially stop, no matter the supply. The reveal will happen right after the gates will be closed.'
            />
            <Faq
                question='Limit per wallet?'
                answer= 'No limit per wallet. We have chosen to create our collection using the ERC721A standard, meaning that you can mint as many Moggies as you want, for close to the cost of one in terms of gas fees. If you ever dreamt of having a lot of cats, the time has come ! 10 000 Moggies will be available to mint. '
            />
            <Faq
                question='Do I own the IP rights to my Moggie Rescue Club NFT?'
                answer= 'Each Moggie holder owns their NFT’s full IP. By becoming a member of the Moggie Rescue Club, you are free to use the image of your Moggie as long as you own the NFT. When selling your NFT on the secondary market, you must cease using it for profit. Owning a Moggie from the Moggie Rescue Club collection does not entitle one to own the Moggie Rescue Club logo or other art besides their own NFT.'
            />
            <Faq
                question='Will the public be able to see where the funds are moved?'
                answer="Transparency is crucial for us. If the Moggie Rescue Club collection is sold out within 10 days, 
                50 % of the mint profits will go to <linkhere>moggierescueclub.eth<linkhere>, 
                ready to be sent to the crypto wallets of carefully chosen charitable organizations related to cats. 
                The remaining 50 % of the mint profits will be sent to <linkhere>moggiesanctuary.eth<linkhere>, 
                in order to fulfill our next phase : create and run the Moggie Rescue Club Sanctuary IRL. Royalties from the secondary market will also go to <linkhere>moggiesanctuary.eth<linkhere> to support the project in the long run.
                "
            />
            <Faq
                question='What is the second collection and when/how to mint?'
                answer="Holders of the first Moggie Rescue Club Collection will be able to mint a new NFT from Collection 2 (name not available yet). To sum up : one Moggie Rescue Club NFT held in a wallet = the right to mint one Collection 2 NFT. This new collection will happen when the Moggie Rescue Club Collection will be sold out, and profits already shared. The goal of this new collection will be to launch the most ambitious phase of the project : Build the Moggie Rescue Club Sanctuary IRL."
            />
            <Faq
                question='What about the Moggie Rescue Club Sanctuary?'
                answer="To create the Sanctuary, we will use the mint profits and royalties, as planned on the roadmap. How much we will have for this will depend on the project’s route. 
                        In order to help the project personally, we have already added 5 Otherside NFTs from our own vault to the balance of <linkhere>moggiesanctuary.eth<linkhere>.
                        <splithere>
                        We will create a legal structure, acquire real estate (in EU), renovate as requested by the usage, norms, standards and rules in force, hire qualified professionals for specific tasks, buy specialized food and medications, secure the place upon the cats specific needs, buy furniture and equipment, pay taxes and bills related to the place, and pay for other potential expenses directly related to the sanctuary and the project.
                        "
            />
              <Faq
                question='What if the collection is not sold out within 10 days?'
                answer="If the Moggie Rescue Club Collection is not sold out within 10 days from the mint date, we will directly jump from phase 2 to Phase 4 of the Roadmap, and the funds will be used differently.
                        <splithere>
                        In this scenario, money generated by the mint of the Moggie Rescue Club collection will not be split between charitable funds, but used to create the Moggie Rescue Club Sanctuary,
                        as planned in Phase 4. After the mint period, funds will be moved from <linkhere>moggierescueclub.eth<linkhere> to <linkhere>moggiesanctuary.eth<linkhere>.
                        <splithere>
                        Nonetheless, even if we are using mint profits differently, we have to remain fair towards our community and the existing charitable funds that we wanted to help in the first place. In case of not selling out, royalties from the Moggie Rescue Club collection will not be sent to <linkhere>moggiesanctuary.eth<linkhere>, but will all go equally to the chosen charitable funds. Each chosen charitable fund will receive a monthly ETH transfer from us, depending on the amount of royalties collected.
                        "
            />
               <Faq
                question='What are the perks for Holders?'
                answer="
                        By buying and holding an NFT from the collection, you become an official member of the Club.
                        <splithere>
                        Holders of Moggie Rescue Club collection will have access to live cams from the Sanctuary to see how the cats are living and taken care of. By using their wallet, holders will be able to connect with the Sanctuary for an exclusive experience.
                        <splithere>
                        We’re also brainstorming about other immersive options. Holders will also be eligible for the virtual adoption of a cat in particular, most likely cats that will be considered as hardly adoptable IRL according to their health or age, and will most likely live forever in the Sanctuary.
                        <splithere>
                        We will create 1:1 NFTs, matching each of these exceptional cats, that will be the proof of virtual adoption via the Blockchain. The money from the mint+secondary corresponding to each of these 1:1’s will be used to pay for the expenses related to the cat’s health or disability. Transparency will be guaranteed and we’ll make the expenses related to this cat available for the holder to check.
                        <splithere>
                        High quality merch will be exclusively available for members of the Moggie Rescue Club. The profits resulting from any sale will be used to cover expenses related to the project and the Sanctuary. A voting system will be also added, so the Club members can be decisionary in the plans.
                    "
            />
        </div>
    </div>
  );
}

export default FaqSection;
