import React, { useEffect, useState } from 'react'
import './MintSection.scss';

import loading from '../../assets/images/loading.svg'
import mp4 from '../../assets/images/unknown.mp4'
import webm from '../../assets/images/unknown.webm'



function MintSection() {
  const[mintAmount,setMintAmount]=useState(1)
  const[limit,setLimit]=useState(Infinity)

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {

    let newMintAmount = mintAmount + 1;
    if (newMintAmount > limit) {
      newMintAmount = limit;
    }
    if(limit===0){
      newMintAmount=1;
    }
    setMintAmount(newMintAmount);
  };

  const setAmountInput = (amount) => {
    amount = Number(amount)
    if (isNaN(amount)) return;
    if (amount > limit) {
      setMintAmount(limit)
    }
    else {
      setMintAmount(amount)
    }
  }

  return (
 
    <div className='MintSection'>
      <div className='mint-section-main'>
        <div className='MintSection-inner'>
          <div className='MintSection-left'>
            <span  className='fs-m tc-green ffam-medium '>
              MOGGIE RESCUE CLUB
            </span>
            <br/>
            <br/>
            <span className="fs-ms tc-white ffam-medium">
              MINTING IN OCT. 2022
            </span>
            <br/>
            <div className='MintSection-left-bottom'>
              <div className='MintSection-amount '>

                {/* <div className='MintSection-amount-top'>
                  <button onClick={() => { decrementMintAmount() }} className='mint-plus-btn   fs-s bold   tc-white'>
                    -
                  </button>
                  <input onChange={(e) => { setAmountInput(e.target.value) }} value={mintAmount} placeholder='1' className='fs-s  tc-white' />
                  <button onClick={() => { incrementMintAmount() }} className='mint-plus-btn  fs-s bold  tc-white'>
                    +
                  </button>
                </div>

                <div className='MintSection-amount-bottom fs-xs tc-white'>
                  <button onClick={() => {}} className='mint-button'>
                    <span className='fs-s ffam-bold '>
                      Connect Wallet 
                    </span>
                  </button>
                </div> */}

              </div>
              
            </div>
          </div>
          <div className='Mint-section-right'>
          
            <video autoPlay playsInline muted loop>
              <source src={webm}></source>
              <source src={mp4}></source>
            </video>
          </div>
        </div>
      </div>
     
    </div>
  )
}

export default MintSection
