import React from 'react'
import './Charity.scss'

import img4 from '../../assets/images/roadmap/cat-left.webp'

import charity1 from '../../assets/images/charities/1.png'
import charity2 from '../../assets/images/charities/2.png'
import charity3 from '../../assets/images/charities/3.png'
import charity4 from '../../assets/images/charities/4.png'

function Charity() {



    const openLink = (link) => {
        window.open(link)
    }

    const navigateTo = (location) => {


        var height = document.getElementsByClassName("header")[0].offsetHeight;
  
        var container = document.getElementsByClassName('App')[0]
        var offsetTop;

        switch (location) {

        
            case "roadmap":
                offsetTop = document.getElementsByClassName("roadmap")[0].offsetTop - height;
                container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
                break;
        
            default:
                break;
        }

    }




    return (
        <div className='charity'>
            <span className='tc-gold fs-l ffam-regular'>CHARITY</span>
            <div className='charity-inner'>
                <div className='charity-left'>
                    <img src={img4}/>
                </div>
                <p className='tc-white ffam-light fs-s'>
                    Our project is a long term play, we’re here to build something that will last, and our first step is helping charities that were here before our project was born. In order to do so, we’ve reached out to organizations that work hard to give stray cats the life they deserve after the trouble they’ve all been facing : Some of them have been living in the streets, others have been abused by their owners, and many of them are sick or old, so it’s a struggle for them to find a home.
                    <br/>
                    <br/>
                    Mint and royalties profits will be shared with each chosen charitable organization via ETH transfers. Helping as a whole community is easier than helping as individuals, and that’s the purpose of the Moggie Rescue Club.
                    <br/>
                    <br/>
                    We’re always looking for more charitable organizations to help, and will be asking the Club Members to share suggestions with us regularly.
                    <br/>
                    <br/>
                    Want to know more about the profits and royalties distribution?
                    <br/>
            
                    Check the <a className='tc-gold' onClick={()=>{navigateTo('roadmap')}}>roadmap</a>.
                </p>
               
                
            </div>
            <div className='tc-gold fs-ms ffam-regular'>SOME CHARITIES WE'LL BE HELPING</div>

            <div className='charity-list'>
                <button onClick={()=>{openLink('https://www.canopycatrescue.com/')}}>
                    <img src={charity1}/>
                </button>
                <button onClick={()=>{openLink('https://www.neighborhoodcats.org/')}}>
                    <img  src={charity2} className='charity-list-2'/>
                </button>
                <button onClick={()=>{openLink('https://heavenlypets.org/')}}>
                    <img src={charity3}/>
                </button>
                <button onClick={()=>{openLink('https://edch.org.uk/')}}>
                    <img src={charity4}/>
                </button>
            </div>
        </div>
    )
}

export default Charity